export default{
	data(){
		return{
			txt:'编辑',
			xuanZhong:[],
			useUser:this.user,
			gwcArr:[],
			url:this.url1,
			zongjia:0,
			flag:false,
			shop:[]
		}
	},
	watch:{
		gwcArr(){
			this.qx()
		},
		xuanZhong(){
			this.zj()
			this.qx()
		}
	},
	methods:{
		jieSuan:function(){
			if(this.user=='未登录'){
				uni.navigateTo({
					url:'../login/index/index'
				})
			}else{
				if(this.xuanZhong.length>0){
					let arr=[]
					for(let i=0;i<this.xuanZhong.length;i++){
						arr.push(this.gwcArr[this.xuanZhong[i]])
					}
					uni.navigateTo({
						url:'gm/gm?shopping='+JSON.stringify(arr)+'&heJi='+this.zongjia
					})
				}else{
					uni.showToast({
						title:'请选择商品',
						icon:'none'
					})
				}
			}
		},
		goShopping:function(a){
			let that=this;
			let usurl2=that.url1+"/dinghai/shop/byid";
			uni.showLoading({
				title: '正在获取',
				mask: true
			});
			uni.request({
			    url: usurl2,
				header:{
					"Content-Type" : "application/x-www-form-urlencoded"
				},
				method:'GET',
			    data: {
					uid:a.uid
				},
			    success: (res) => {
					if(res.statusCode==200){
						uni.showToast({
							title: '加载成功'
						});
						console.log(res)
						uni.navigateTo({
							url:"../index/goShopping/goShopping?goShopping="+JSON.stringify(a)+'&shop='+JSON.stringify(res.data.data),
							complete(res){
								console.log(res)
							}
						})
					}else{
						uni.showToast({
							title: '您的网络打了个盹！',
							icon:"none"
						});
					}
			    },
				fail : function(err){
					console.log(err)
					uni.showToast({
						title: '您的网络打了个盹！',
						icon:"none"
					});
				}
			})
		},
		shanChu:function(){
			if(this.user=='未登录'){
				uni.navigateTo({
					url:'../login/index/index'
				})
			}else{
				for(let i=0;i<this.xuanZhong.length;i++){
					this.shanChuReq(this.gwcArr[this.xuanZhong[i]].shoppingid,this.xuanZhong[i],i)
				}
			}
		},
		shanChuReq:function(id,i,o){
			let that=this;
			uni.request({
					url: that.url1+"/dinghai/Shopping/deleteShopping",
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						shoppingid:id
					},
			    success: (res) => {
					console.log(res)
					if(res.statusCode==200){
						that.gwcArr.splice(i,1),
						that.xuanZhong.splice(o,1)
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		qx:function(){
			if(this.gwcArr.length!=0){
				if(this.gwcArr.length==this.xuanZhong.length){
					this.flag=true
				}else{
					this.flag=false
				}
			}else{
				this.flag=false
			}
		},
		zj:function(){
			let p=0;
			for(let i=0;i<this.xuanZhong.length;i++){
				p+=((this.gwcArr[this.xuanZhong[i]].shoppingprice+this.gwcArr[this.xuanZhong[i]].freight)*this.gwcArr[this.xuanZhong[i]].shoppingnum)
			}
			this.zongjia=p;
		},
		quanXuan:function(){
			if(this.xuanZhong.length!=this.gwcArr.length){
				let arr=[]
				for(let i=0;i<this.gwcArr.length;i++){
					arr.push(i)
				}
				this.xuanZhong=arr
			}else{
				this.xuanZhong=[]
			}
		},
		dian:function(i){
			this.xuanZhong.indexOf(i)==-1?this.xuanZhong.push(i):this.xuanZhong.splice(this.xuanZhong.indexOf(i),1)
		},
		del:function(item){
			item.shoppingnum<=1?'':item.shoppingnum--;
			this.zj()
		},
		add:function(item){
			item.shoppingnum++;
			this.zj()
		},
		get:function(){
			console.log(this.useUser.uid)
			let that=this;
			let usurl=that.url1+"/dinghai/Shopping/selectAllShopping";
			uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						uid:that.useUser.uid
					},
			    success: (res) => {
					console.log(res)
					if(res.statusCode==200){
						that.gwcArr=res.data.flag
					}else{
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"您的网络打了个盹",
						icon:"none"
					})
				}
			});
		},
		getArr:function () {
			let that=this;
			let usurl=that.url1+"/dinghai/commodity/getbytypeall";
			uni.showLoading({
				title:'正在加载',
				mask: true
			})
			uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'GET',
						data: {
							type:'家纺',
							start:1,
							size:20
						},
				    success: (res) => {
						console.log(res)
						if(res.data.code==200){
							that.shop=res.data.data;
							for(let i=0;i<that.shop.length;i++){
								that.shop[i].img1=JSON.parse(that.shop[i].img1);
								that.shop[i].img2=JSON.parse(that.shop[i].img2);
								// that.shop[i].shopType=JSON.parse(that.shop[i].shopType);
								// that.shop[i].label=JSON.parse(that.shop[i].label)
							}
							uni.showToast({
								title:"加载成功",
								icon:"none"
							})
						}else{
							uni.showToast({
								title:"您的网络打了个盹",
								icon:"none"
							})
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title:"您的网络打了个盹",
							icon:"none"
						})
					}
				});
		},
	},
	onLoad() {
		if(this.user=='未登录'){
			uni.navigateTo({
				url:'../login/index/index'
			})
		}else{
			this.get();
			this.getArr()
			this.xuanZhong=[]
		}
	},
	onShow(){
		this.get();
		this.getArr()
		this.xuanZhong=[]
	}
}
